import Joi from 'joi'
import chalk from 'chalk'

const defaultValue = {
	contentBase: 'dist'
}
const propType = {
	devServer: Joi.object().keys({
		contentBase: Joi.string().trim().min(3).required()
	}).required()
}

export default (devServer, getDefaultValue = false) => {
	if (getDefaultValue) {
		return defaultValue
	}

	const {error, value} = Joi.validate({devServer}, propType)

	if (error !== null) {
		console.error(error.message)
		console.log(chalk.bold.yellow(`Current value: "${JSON.stringify(devServer, null, 2)}"`))
		console.log(chalk.bold.cyan(`We are using: "${JSON.stringify(defaultValue, null, 2)}"`))
		return defaultValue
	}
	return value.devServer
}